:host {
  display: block;
  font: var(--gcds-footer-font);

  slot {
    display: inherit;
  }

  .sub__header,
  .themenav__header,
  .gcds-footer__header {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    margin: 0;
    overflow: hidden;
    position: absolute;
    width: 1px;
  }

  [class$='__container'] {
    justify-content: space-between;
    width: 90%;
    max-width: var(--gcds-footer-container-width);
    margin: var(--gcds-footer-container-margin);
  }

  nav {
    position: relative;

    li {
      margin: var(--gcds-footer-listitem-margin);
    }
  }

  [class$='__list'] {
    list-style: none;
    padding: var(--gcds-footer-list-padding);
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: var(--gcds-footer-list-grid-gap);

    li a {
      color: var(--gcds-footer-main-text);
      text-underline-position: under;

      &:not(:hover) {
        text-decoration: none;
      }

      &:focus {
        color: var(--gcds-footer-link-focus-text);
        background-color: var(--gcds-footer-link-focus-background);
        border-radius: var(--gcds-footer-link-focus-radius);
        text-decoration: none;
        box-shadow: var(--gcds-footer-link-focus-box-shadow);
        outline-offset: var(--gcds-footer-link-focus-outline-offset);
        outline: var(--gcds-footer-link-focus-outline);
      }
    }
  }

  @media screen and (min-width: 48em) {
    [class$='__list'] {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media screen and (min-width: 64em) {
    [class$='__list'] {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .gcds-footer__contextual {
    background-color: var(--gcds-footer-contextual-background);
    color: var(--gcds-footer-contextual-text);
    padding: var(--gcds-footer-contextual-padding);
  }

  .gcds-footer__main {
    background-color: var(--gcds-footer-main-background);
    background-image: url(data:image/png;base64,);
    background-position: right bottom;
    background-repeat: no-repeat;
    color: var(--gcds-footer-main-text);

    nav {
      &:first-of-type::after {
        border-bottom: var(--gcds-footer-main-nav-first-after-border-width) solid var(--gcds-footer-main-nav-first-after-border-color);
        content: '';
        display: block;
        width: var(--gcds-footer-main-nav-first-after-width);
      }

      &.main__govnav {
        padding: var(--gcds-footer-main-govnav-padding);
      }

      &.main__themenav {
        padding: var(--gcds-footer-main-themenav-padding);

        .themenav__list {
          display: grid;
          grid-template-rows: repeat(18, 1fr);
          grid-auto-flow: column;
        }

        @media screen and (min-width: 48em) {
          .themenav__list {
            grid-template-rows: repeat(9, 1fr);
          }
        }

        @media screen and (min-width: 64em) {
          .themenav__list {
            grid-template-rows: repeat(6, 1fr);
          }
        }
      }
    }
  }

  .gcds-footer__sub {
    background-color: var(--gcds-footer-sub-background);

    .sub__container {
      display: grid;
      grid-gap: var(--gcds-footer-sub-grid-gap);
      grid-template-columns: 3fr 1fr;
      grid-template-areas: 'list wordmark';

      nav {
        grid-area: list;
        padding: var(--gcds-footer-sub-nav-padding);
        display: flex;
        align-items: center;
      }

      slot[name='wordmark'],
      .sub__wordmark {
        grid-area: wordmark;
        display: flex;

        gcds-signature {
          align-self: end;
          width: var(--gcds-footer-sub-signature-md-width);
        }

        @media screen and (min-width: 30em) {
          gcds-signature {
            width: var(--gcds-footer-sub-signature-lg-width);
            min-width: var(--gcds-footer-sub-signature-lg-min-width);
            margin: var(--gcds-footer-sub-signature-margin);
          }
        }

        @media screen and (max-width: 20em) {
          gcds-signature {
            width: var(--gcds-footer-sub-signature-sm-width);
          }
        }

        @media screen and (min-width: 64em) {
          display: inline-block;

          gcds-signature {
            margin: var(--gcds-footer-sub-signature-lg-margin);
          }
        }
      }

      a {
        color: var(--gcds-footer-sub-text);
        text-decoration: none;

        &:hover {
          color: var(--gcds-footer-sub-hover-text);
          text-underline-offset: 0.2em;
          text-decoration: underline;
          text-decoration-thickness: var(--gcds-footer-sub-hover-decoration-thickness);
        }

        &:focus {
          color: var(--gcds-footer-link-focus-text);
          background-color: var(--gcds-footer-link-focus-background);
          border-radius: var(--gcds-footer-link-focus-radius);
          text-decoration: none;
          box-shadow: var(--gcds-footer-link-focus-box-shadow);
          outline-offset: var(--gcds-footer-link-focus-outline-offset);
          outline: var(--gcds-footer-link-focus-outline);
        }
      }
    }

    ul {
      padding: 0;
      margin: 0;
      list-style-type: none;

      li {
        display: block;

        @media screen and (min-width: 64em) {
          margin-bottom: 0;
          display: inline-block;
        }

        @media screen and (min-width: 64em) {
          &:first-of-type:before {
            content: '';
            margin: 0;
          }

          &:before {
            content: '\2022';
            margin: var(--gcds-footer-sub-listitem-before-margin);
            display: inline;
          }
        }

        a {
          text-decoration: none;
          transition: color 0.15s;
        }
      }
    }
  }
}
